footer {
    background-color: $blue-bg;
    color: #fff;
    padding: 30px 0 0;
    position: relative;

    .footer-row {
        display: flex;
        flex-wrap: wrap;

        .footer-menu {
            flex: 100%;
            margin-bottom: 30px;

            @media (min-width: $bp-tablet){
                flex:50%;

    }

            @media (min-width: $bp-desktop){
                flex:25%;

    }

            @media (min-width: $bp-wide){
                margin-bottom: 40px;

    }

            h4 {
                text-transform: uppercase;
                font-size: 1rem;
                margin-bottom: 26px;
            }
        }
    }
}
